import React, { Component } from 'react';
import pictureAnimation from '../StyleModules/Pictures.module.css';
import Cop from '../Visuals/Classes/Cop.png';
import Corporate from '../Visuals/Classes/Corporate.png';
import Fixer from '../Visuals/Classes/Fixer.png';
import Media from '../Visuals/Classes/Media.png';
import Netrunner from '../Visuals/Classes/Netrunner.png';
import Nomad from '../Visuals/Classes/Nomad.png';
import Rockerboy from '../Visuals/Classes/Rockerboy.png';
import Solo from '../Visuals/Classes/Solo.png';
import Techie from '../Visuals/Classes/Techie.png';


interface ClassImageProps {
    name: string;
}

const Images = [
    { name: "Cop", image: Cop },
    { name: "Corporate", image: Corporate },
    { name: "Fixer", image: Fixer },
    { name: "Media", image: Media },
    { name: "Netrunner", image: Netrunner },
    { name: "Nomad", image: Nomad },
    { name: "Rockerboy", image: Rockerboy },
    { name: "Solo", image: Solo },
    { name: "Techie", image: Techie}
]

class ClassImage extends Component<ClassImageProps, {}> {

    public findImage = () => {
        for (const classes of Images) {
            if(classes.name === this.props.name) {
                return classes.image;
            }
        }
        return Cop;
    }

    public render() {
        return <img className={pictureAnimation.InterlacedPicture} alt={this.props.name} src={this.findImage()}/>
    }
}

export default ClassImage;