import React, { Component } from 'react';
import pictureAnimation from '../StyleModules/Pictures.module.css';
import Arasaka from '../Visuals/CorporationLogos/Arasaka.png';
import Biotechnica from '../Visuals/CorporationLogos/Biotechnica.png';
import EBM from '../Visuals/CorporationLogos/EBM.png';
import Infocomp from '../Visuals/CorporationLogos/Infocomp.png';
import MAF from '../Visuals/CorporationLogos/MAF.png';
import Microtech from '../Visuals/CorporationLogos/Microtech.png';
import Militech from '../Visuals/CorporationLogos/Militech.png';
import NetworkNews54 from '../Visuals/CorporationLogos/NetworkNews54.png';
import OrbitalAir from '../Visuals/CorporationLogos/OrbitalAir.png';
import Petrochem from '../Visuals/CorporationLogos/Petrochem.png';
import TraumaTeam from '../Visuals/CorporationLogos/TraumaTeam.png';
import WNS from '../Visuals/CorporationLogos/WNS.png';
import WorldSat from '../Visuals/CorporationLogos/WorldSat.png';
import Zetatech from '../Visuals/CorporationLogos/Zetatech.png';

const Corporations = [
    { name: "Arasaka", logo: Arasaka },
    { name: "Biotechnica", logo: Biotechnica },
    { name: "Euro Business Machines", logo: EBM },
    { name: "Infocomp", logo: Infocomp },
    { name: "Merill Asukaga & Finch", logo: MAF },
    { name: "Militech", logo: Militech},
    { name: "Microtech", logo: Microtech },
    { name: "Network News 54", logo: NetworkNews54 },
    { name: "Orbital Air", logo: OrbitalAir },
    { name: "Petrochem", logo: Petrochem },
    { name: "Trauma Team International", logo: TraumaTeam },
    { name: "World News Service", logo: WNS },
    { name: "WorldSat Communcations Network", logo: WorldSat },
    { name: "Zetatech", logo: Zetatech }
]

interface CorporationLogoProps {
    name: string;
}

class NCCorporationLogo extends Component<CorporationLogoProps, {}> {

    public getPictureFromName = () => {
        for (const corp of Corporations) {
            if(this.props.name === corp.name)
                return corp.logo;
        }

        return Zetatech;
    }

	public render() {
		return (
			<img className={pictureAnimation.InterlacedPicture} alt="." title={this.props.name} src={this.getPictureFromName()}/>
		);
	}
}

export default NCCorporationLogo;
